function startProgressAnimation(progressBars, type) {
  for (let path of progressBars) {
    let progress = path.dataset.progress;
    // let fullLengthSVGPath = path.getTotalLength();
    let fullLengthSVGPath = type === 'circle' ? 235.2 : 300;

    path.style.strokeDashoffset = fullLengthSVGPath/100*(100-progress);
  }
}

function startFunctionAnimationOnVisibleObject( object, functionAnimation, param, visiblePercentage ) {
  const checkVisibleThisContainer = () => {
    const winHeight = window.innerHeight;
    const winScrollY = window.scrollY;

    const _object = object;
    const containerOffsetTop = _object.offsetTop;
    const containerHeight = _object.getBoundingClientRect().height;

    const offsetTopIncludeStartProgressAnimation = winHeight + winScrollY - containerHeight*(visiblePercentage || 0.5);

    const resultCheckVisibleThisContainer = offsetTopIncludeStartProgressAnimation >= containerOffsetTop;

    if (resultCheckVisibleThisContainer) {
      functionAnimation(param, 'circle');
      window.removeEventListener('scroll', checkVisibleThisContainer)
    }
  };

  window.addEventListener('scroll', checkVisibleThisContainer );

  checkVisibleThisContainer();
}

function skillClickListener(skills) {
  let involves = document.querySelectorAll('.software-skills__involve');
  for (let skill of skills) {
    skill.addEventListener('click', (e) => {
      let involveList = e.currentTarget.querySelector('.software-skills__involve');
      let SVGsWithAnimationInThis = e.currentTarget.querySelectorAll('.line-progress-bar__container [data-progress]');
      if ( involveList ) {
        if ( involveList.classList.contains('hidden') ) {
          for (let involve of involves) {
            involve.classList.add('hidden');
          }
          involveList.classList.remove('hidden');
          startProgressAnimation(SVGsWithAnimationInThis, 'line');
        } else {
          for (let involve of involves) {
            involve.classList.add('hidden');
          }
        }
      }
    });
  }
}

window.onload = () => {
  let circleProgressBars = document.querySelectorAll('.circle-progress-bar__front');
  let skills = document.querySelectorAll('.software-skills__skill');
  let skillsList = document.querySelector('.software-skills__list');
  let pointers = document.querySelectorAll('[data-name = "lines-to__line"]');
  let pointersId = document.querySelectorAll('#lines-to__line');


  skillClickListener(skills);

  startFunctionAnimationOnVisibleObject(skillsList, startProgressAnimation, circleProgressBars)
};
